<template>
  <div class="container">
    <div class="intro">
      <span class="intro_msg1">나에게 꼭 맞는 학교는 ... </span>
    </div>
    <div class="boxwrapper">
      <div class="show" v-if="isLoading && this.recommended !== true">
        <img src="../assets/flying-airplane.gif" />
      </div>
      <div class="contents" v-if="!isLoading || this.recommended === true">
        <div class="box">
          <img :src="`${firstuniv.univ_image}`" class="box_image" />
          <div class="box_title">
            <span class="title_style">{{ firstuniv.name }}</span>
          </div>
          <div class="box_detail">
            <button class="box_link" @click="detail1">상세 정보 보기</button>
          </div>
        </div>
        <div class="box">
          <img :src="`${seconduniv2.univ_image}`" class="box_image" />
          <div class="box_title">
            <span class="title_style">{{ seconduniv2.name }}</span>
          </div>
          <div class="box_detail">
            <button class="box_link" @click="detail2">상세 정보 보기</button>
          </div>
        </div>
        <div class="box">
          <img :src="`${thirduniv.univ_image}`" class="box_image" />
          <div class="box_title">
            <span class="title_style">{{ thirduniv.name }}</span>
          </div>
          <div class="box_detail">
            <button class="box_link" @click="detail3">상세 정보 보기</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import axios from 'axios'
export default {
  components: {},
  data() {
    return {
      univlist: [],
      firstuniv: {},
      seconduniv: {},
      thirduniv: {},
      country: '',
      criteria: [],
      weather: '',
      language: [],
      semester: '',
      college: '',
      found: false,
      senddata: {
        country: this.country,
        semester: this.semester,
        college: this.college,
        criteria: this.criteria,
        wlevel: this.wlevel,
        language: this.language
      },
      isLoading: true,
      recommended: false,
      univ1: '',
      seconduniv2: {}
    }
  },
  setup() {},
  create() {},
  mounted() {
    this.country = sessionStorage.getItem('country')
    this.criteria = sessionStorage.getItem('criteria')
    this.weather = sessionStorage.getItem('weather')
    this.language = sessionStorage.getItem('language')
    this.semester = sessionStorage.getItem('semester')
    this.college = sessionStorage.getItem('college')
    if (sessionStorage.getItem('recommended') === 'true') {
      this.recommended = true
    }
    this.getData()
    this.univ1 = this.firstuniv.name
    this.getData_2()
  },
  unmounted() {},
  methods: {
    async getData() {
      try {
        const res = await axios.get('/api/recommend/', {
          params: {
            country: this.country,
            criteria: this.criteria,
            college: this.college,
            weather: this.weather,
            language: this.language,
            semester: this.semester
          }
        })
        this.firstuniv = res.data
        sessionStorage.setItem('recommended', 'true')
        setTimeout(this.recommend, 3000)
        setTimeout(this.loading, 3000)
      } catch (err) {
        console.error(err)
      }
    },
    async getData_2() {
      try {
        const res = await axios.get('/api/tsneRecommend/', {
          params: {
            name: this.univ1
          }
        })
        console.log(res.data)
        this.seconduniv2 = res.data[0]
      } catch (err) {
        console.error(err)
      }
    },
    detail1() {
      this.$router.push({
        name: 'detailresult',
        query: { name: this.firstuniv.name }
      })
    },
    detail2() {
      this.$router.push({
        name: 'detailresult',
        query: { name: this.seconduniv2.name }
      })
    },
    detail3() {
      this.$router.push({
        name: 'detailresult',
        query: { name: this.thirduniv.name }
      })
    },
    loading() {
      this.isLoading = false
    },
    recommend() {
      this.recommended = true
    }
  }
}
</script>
